
import { defineComponent, onMounted } from "vue";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import { setCurrentPageBreadcrumbs } from "@/core/helpers/breadcrumb";
import { useStore } from "vuex";
import MixedWidget14 from "@/components/widgets/mixed/Widget14.vue";
import PlansAction from "@/components/widgets/mixed/PlansAction.vue";
import { useRouter } from "vue-router";
import {
  Activite,
  GraviteDangerosite,
  PosteTravail,
  Service,
  Theme,
  Zone,
  Aspect,
  Impact,
  Situation,
  Gravite,
  Frequence,
  Generic,
} from "@/core/data/ival";
import { useRoute } from "vue-router";
import axios from "axios";
import MixedWidget1 from "@/components/widgets/mixed/Widget1.vue";
import TableWidgetPlan from "@/components/widgets/tables/WidgetPlan.vue";
export default defineComponent({
  components: {
    MixedWidget14,
    TableWidgetPlan,
  },
  computed: {
    currentRouteName() {
      return window.location.origin;
    },
    rules() {
      const rules = [
        (value) => {
          if (value) return true;
          return "Champ obligatoire";
        },
      ];
      return rules;
    },
  },
  name: "customer-details",
  data() {
    return {
      add: true,
      niveau_m: 0,
      data2: {},
      listValRef: {
        activites: [],
        processuses: [],
        services: [],
        zones: [],
        poste_travails: [],
        themes: [],
        aspects: [],
        impacts: [],
        situations: [],
        frequences: [],
        gravite_etendus: [],
        gravite_dangerosites: [],
        gravite_persistances: [],
        moyen_maitrise_humains: [],
        moyen_maitrise_techniques: [],
        moyen_maitrise_organisations: [],
        niveau_priorites: [],
        niveau_maitrises: [],
        plan_actions: [],
      },
      activites: [],
      services: [],
      zones: [],
      poste_travails: [],
      gravite_dangerosites: [],
      gravite_dangerosite_irs: [],
      data: {
        moyen_maitrise_humains: [],
        moyen_maitrise_techniques: [],
        moyen_maitrise_organisations: [],
        plan_actions: [],
        niveau_priorite: 1,
        niveau_maitrise: 0,
        processus: null as unknown as Generic,
        service: null as unknown as Service,
        activite: null as unknown as Activite,
        zone: null as unknown as Zone,
        poste_travail: null as unknown as PosteTravail,
        aspect: null as unknown as Aspect,
        impact: null as unknown as Impact,
        situation: null as unknown as Situation,
        delai: new Date().toISOString().substr(0, 10),
        menu2: false,
        s_ns: false,
        theme: null as unknown as Theme,
        frequence: null as unknown as Frequence,
        gravite_etendu: null as unknown as Gravite,
        gravite_dangerosite: null as unknown as Gravite,
        gravite_persistance: null as unknown as Gravite,
        frequence_ir: null as unknown as Frequence,
        gravite_etendu_ir: null as unknown as Gravite,
        gravite_dangerosite_ir: null as unknown as Gravite,
        gravite_persistance_ir: null as unknown as Gravite,
        impact_residuel: 0,
      },
      form: {
        zone_id: null as unknown as number,
        processus_id: null as unknown as number,
        service_id: null as unknown as number,
        activite_id: null as unknown as number,
        poste_travail_id: null as unknown as number,
        aspect_id: null as unknown as number,
        impact_id: null as unknown as number,
        theme_id: null as unknown as number,
        situation_id: null as unknown as number,
        frequence_id: null as unknown as number,
        gravite_etendu_id: null as unknown as number,
        gravite_dangerosite_id: null as unknown as number,
        gravite_persistance_id: null as unknown as number,
        frequence_ir_id: null as unknown as number,
        gravite_etendu_ir_id: null as unknown as number,
        gravite_dangerosite_ir_id: null as unknown as number,
        gravite_persistance_ir_id: null as unknown as number,
        s_ns: false,
        niveau_maitrise_id: null as unknown as number,
        niveau_priorite_id: null as unknown as number,
        moyen_maitrise_humains: [] as unknown as number[],
        moyen_maitrise_organisations: [] as unknown as number[],
        moyen_maitrise_techniques: [] as unknown as number[],
        plan_actions: [] as unknown as number[],
        delai: new Date().toISOString().substr(0, 10),
        impact_residuel: null,
      },
    };
  },
  watch: {
    "data.delai"() {
      this.form.delai = this.data.delai;
    },
    "data.theme"() {
      this.form.theme_id = this.data.theme.id;
      this.gravite_dangerosites = this.listValRef.gravite_dangerosites.filter(
        (gd: GraviteDangerosite) => {
          return gd.theme_id === this.data.theme.id;
        }
      );
      this.gravite_dangerosite_irs =
        this.listValRef.gravite_dangerosites.filter(
          (gd: GraviteDangerosite) => {
            return gd.theme_id === this.data.theme.id;
          }
        );
    },
    "data.processus"() {
      this.form.processus_id = this.data.processus.id;
      this.services = this.listValRef.services.filter((service: Service) => {
        return service.processus_id === this.data.processus.id;
      });
    },
    "data.service"() {
      this.form.service_id = this.data.service.id;
      this.activites = this.listValRef.activites.filter(
        (activity: Activite) => {
          return activity.service_id === this.data.service.id;
        }
      );
    },
    "data.activite"() {
      this.form.activite_id = this.data.activite.id;
      this.zones = this.listValRef.zones.filter((zone: Zone) => {
        return zone.activite_id === this.data.activite.id;
      });
    },
    "data.zone"() {
      this.form.zone_id = this.data.zone.id;
      this.poste_travails = this.listValRef.poste_travails.filter(
        (poste_travail: PosteTravail) => {
          return poste_travail.zone_id === this.data.zone.id;
        }
      );
    },
    "data.poste_travail"() {
      this.form.poste_travail_id = this.data.poste_travail.id;
    },
    "data.aspect"() {
      this.form.aspect_id = this.data.aspect.id;
    },
    "data.impact"() {
      this.form.impact_id = this.data.impact.id;
    },
    "data.situation"() {
      this.form.situation_id = this.data.situation.id;
    },
    "data.frequence"() {
      this.form.frequence_id = this.data.frequence.id;
    },
    "data.gravite_etendu"() {
      this.form.gravite_etendu_id = this.data.gravite_etendu.id;
    },
    "data.gravite_dangerosite"() {
      this.form.gravite_dangerosite_id = this.data.gravite_dangerosite.id;
    },
    "data.gravite_persistance"() {
      this.form.gravite_persistance_id = this.data.gravite_persistance.id;
    },
    "data.frequence_ir"() {
      this.form.frequence_ir_id = this.data.frequence_ir.id;
    },
    "data.gravite_etendu_ir"() {
      this.form.gravite_etendu_ir_id = this.data.gravite_etendu_ir.id;
    },
    "data.gravite_dangerosite_ir"() {
      this.form.gravite_dangerosite_ir_id = this.data.gravite_dangerosite_ir.id;
    },
    "data.gravite_persistance_ir"() {
      this.form.gravite_persistance_ir_id = this.data.gravite_persistance_ir.id;
    },
    "data.niveau_m"() {
      let instance: Generic;
      instance = this.listValRef.niveau_maitrises[this.niveau_m];
      this.form.niveau_maitrise_id = instance.id;
    },
    "data.niveau_priorite"() {
      let instance: Generic;
      console.log(this.listValRef.niveau_priorites);
      // instance = this.listValRef.niveau_priorites[this.data.niveau_priorite];
      // if (instance) this.form.niveau_priorite_id = instance.id;
    },
    "data.moyen_maitrise_humains"() {
      let instance: Array<Generic>;
      instance = this.data.moyen_maitrise_humains;
      this.form.moyen_maitrise_humains = instance.map((e) => e.id);
    },
    "data.moyen_maitrise_organisations"() {
      let instance: Array<Generic>;
      instance = this.data.moyen_maitrise_organisations;
      this.form.moyen_maitrise_organisations = instance.map((e) => e.id);
    },
    "data.moyen_maitrise_techniques"() {
      let instance: Array<Generic>;
      instance = this.data.moyen_maitrise_techniques;
      this.form.moyen_maitrise_techniques = instance.map((e) => e.id);
    },
    "data.plan_actions"() {
      let instance: Array<Generic>;
      instance = this.data.plan_actions;
      this.form.plan_actions = instance.map((e) => e.id);
    },
  },
  beforeMount() {
    if (this.$route.params.id) {
      this.add = false;
    }
    this.fetch();
  },
  methods: {
    async updateData() {
      const router = useRouter();
      var data = this.form;

      var config = {
        method: "put",
        url: this.currentRouteName + "/api/analyse/" + this.$route.params.id,
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then((response) => {
          Swal.fire({
            text: "Analyse environnementale bien Modifié",
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok",
            customClass: {
              confirmButton: "btn fw-bold btn-light-primary",
            },
          }).then((data) => {
            // Go to page after successfully login
            this.$router.push({
              name: "apps-analyse-show-analyse",
              params: { id: this.$route.params.id },
            });
          });
        })
        .catch(function (error) {
          Swal.fire({
            text: error,
            icon: "error",
            buttonsStyling: false,
            confirmButtonText: "Réessayer!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-danger",
            },
          });
        });
    },
    async saveData() {
      const router = useRouter();
      var data = this.form;

      var config = {
        method: "post",
        url: this.currentRouteName + "/api/analyse",
        headers: {
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then((response) => {
          Swal.fire({
            text: "Analyse environnementale bien Ajoutée",
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok",
            customClass: {
              confirmButton: "btn fw-bold btn-light-success",
            },
          }).then((data) => {
            this.$router.push({
              name: "apps-analyse-analyse-env-list",
              params: { id: this.$route.params.id },
            });
          });
        })
        .catch(function (error) {
          Swal.fire({
            text: error,
            icon: "error",
            buttonsStyling: false,
            confirmButtonText: "Réessayer!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-danger",
            },
          });
        });
    },
    async fetch() {
      const responseValRef = await axios.get(
        this.currentRouteName + "/api/valeur-ref"
      );
      this.listValRef = responseValRef.data;
      await this.fetchAnalyse();
    },
    async fetchAnalyse() {
      if (this.$route.params.id) {
        const analyse = await axios.get(
          this.currentRouteName + "/api/analyse/" + this.$route.params.id
        );
        this.data = analyse.data;
        console.info(this.data);
      }
    },
  },

  setup() {
    const route = useRoute();
    onMounted(() => {
      setCurrentPageBreadcrumbs(
        route.params.id ? "Modifier AE-" + route.params.id : "Ajouter une AE",
        ["Accueil", "Analyse environnementale"]
      );
    });

    return {};
  },
});
